import React, {useState} from 'react';
import {useSelector} from "react-redux";
import {useNavigate} from "react-router-dom";

function Pay() {
    let state = useSelector((state) => {
        return state;
    });

    let navigate = useNavigate();

    function onClickPayment() {

        const {IMP} = window;
        IMP.init('imp02185252');

        const data = {
            pg: 'kakaopay',
            pay_method: 'card',
            merchant_uid: `mid_${new Date().getTime()}`,
            amount: state.totalprice,
            name: ''
        };

        IMP.request_pay(data, callback);
    }

    function callback(response) {
        const {
            success,
            merchant_uid,
            error_msg,
        } = response;

        if (success) {
            let pur = JSON.parse(localStorage.getItem("buy") || "[]");

            let today = new Date();

            let year = today.getFullYear(); // 
            let month = today.getMonth() + 1;  // 
            let date = today.getDate();  // 
            let day = today.getDay();  // 

            switch (day){
                case 1: day = ''; break;
                case 2: day = ''; break;
                case 3: day = ''; break;
                case 4: day = ''; break;
                case 5: day = ''; break;
                case 6: day = ''; break;
                case 7: day = ''; break;
            }

            let buydate = year + '/' + month + '/' + date + '/' + day;


            pur.push({userid: localStorage.getItem('userId'), seatnum:[...state.selectseat],
            cate: localStorage.getItem('gocategory'), cateid: localStorage.getItem('performanceId'),
            purdate: buydate});

            localStorage.setItem('buy', JSON.stringify(pur));

            alert(' ');
            navigate('/success');
        } else {
            alert(` : ${error_msg}`);
            window.location.href = '/seat';
        }
    }

    return (

        <div>
            {onClickPayment()}
        </div>

    );
}

export default Pay;