import { useSelector } from "react-redux";
import ConcertdetailBody from "./Concertdetailbody";
import {useNavigate} from "react-router-dom";
import 'react-calendar/dist/Calendar.css'; // css import
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { useState} from "react";
import { ko } from "date-fns/esm/locale";
import '../css/Detailheader.css'
import moment from "moment";
import 'react-calendar/dist/Calendar.css'; // css import

function Concertdetailheader() {

    let state = useSelector((state) => state)

    let find = state.concert[localStorage.getItem('performanceId')]

    let navigate = useNavigate();

    return (
        <div>
            <div className="renew-wrap">
                <div className="renew-content">

                    <div className="rn-02">
                        <p className="rn-big-title">{find.title}</p>
                    </div>
                </div>
            </div>
            <div className="movie-pop-wrap">
            </div>
            <div className="renew-wrap rw2">
                <div className="renew-content">
                    <div className="rn-03">
                        <div className="rn-03-left">
                            <div className="rn-product-imgbox">
                                <img src={find.imageadr} />
                            </div>
                        </div>
                        <div className="rn-03-right">
                            <div className="rn-product-area1">
                                <dl>
                                    <dt></dt>
                                    <dd>&nbsp;{find.rate}</dd>
                                    <dt></dt>
                                    <dd>&nbsp;{find.showtime}</dd>
                                    <dt></dt>
                                    <dd>&nbsp;<a className='rn-product-peole'>{find.cast}</a></dd>
                                    <dt id="dtPrice"></dt>
                                    <dd id="divPrice" className="rn-product-price">
                                        <ul className="rn-product-price1">
                                            <li>VIP <span className='rn-red'>{find.priceVIP}</span></li>
                                            <li>SR <span className='rn-red'>{find.priceSR}</span></li>
                                            <li>R <span className='rn-red'>{find.priceR}</span></li>
                                            <li>S <span className='rn-red'>{find.priceS}</span></li>
                                        </ul>
                                    </dd>
                                    <dd className="rn-product-dc"></dd>
                                </dl>
                            </div>

                            <div className="rn-product-area3">
                                <dl>
                                    <dt> </dt>
                                    <dd>{find.showyear} {find.showmonth} {find.showday}  {find.starttime}</dd>
                                    <dt id="deliveryTitle"></dt>
                                    <dd>{find.delivery}</dd>
                                </dl>
                            </div>

                        </div>
                    </div>
                    <div className="rn-05">
                        <div className="calender2-1">
                            
                                <a className="calender3-1">   </a>
                            
                            <div className="calender3-2">
                                <Calender />
                                <div className="calender4-2">

                                </div>
                            </div>
                        </div>
                        <div className="calender2-2">
                            <div className="calender3-3">

                            </div>
                            <div className="caleder3-4">

                            </div>
                        </div>
                        <a onClick={ () => {
                            if (localStorage.getItem('userId') == ''){
                                alert(' ');
                                navigate('/user/login');
                            }else{
                                state.gocategory = 'concert';
                                navigate('/seat');
                            }
                        } } className='rn-bb03'></a>
                    </div>
                    <ConcertdetailBody/>
                </div>
            </div>
            
        </div>
    )
}

export default Concertdetailheader;

function Calender() {
    let state = useSelector((state) => state);
    const [startDate, setStartDate] = useState(null);
    let showdate = state.concert[localStorage.getItem('performanceId')].showdate
    let showtime = state.concert[localStorage.getItem('performanceId')].starttime
    let vip = state.concert[localStorage.getItem('performanceId')].vipticket
    let sr = state.concert[localStorage.getItem('performanceId')].srticket
    let r = state.concert[localStorage.getItem('performanceId')].rticket
    let s = state.concert[localStorage.getItem('performanceId')].sticket

    let priceVIP = state.concert[localStorage.getItem('performanceId')].priceVIP
    let priceSR = state.concert[localStorage.getItem('performanceId')].priceSR
    let priceR = state.concert[localStorage.getItem('performanceId')].priceR
    let priceS = state.concert[localStorage.getItem('performanceId')].priceS

    return (
        <div>
            <DatePicker
            locale={ko}
            selected={startDate}
            onChange={(date) => setStartDate(date)}
            includeDates={[new Date(), new Date(showdate)]}
            inline
            />
            <p className="calender3-1">
                * {moment(startDate).format("YYYY MM DD")}    *
                {
                    moment(startDate).format("YYYY-MM-DD") === showdate ?
                    <p className="calender3-1-1">
                        <br />
                        <br />
                        {showtime}  .
                        <br />
                        <br />
                        <br />
                        <br />
                         #   
                         <br/>
                         <br/>
                         <div className="ticket">
                            <span className="ticket1"> VIP  {priceVIP} </span> <span className="ticket2"> {vip} </span> <br/>
                            <br/><span className="ticket1">SR  {priceR} </span><span className="ticket2"> {sr} </span> <br/>
                            <br/><span className="ticket1">R  {priceR} </span><span className="ticket2">  {r} </span> <br/>
                            <br/><span className="ticket1">S  {priceS} </span><span className="ticket2">  {s} </span>
                         </div>
                    </p>
                    
                    : <p className="calender3-1-1">
                         .
                    </p>
                }
            </p>
            
        </div>
    );
  };