import {Link, Navigate, useNavigate} from "react-router-dom";
import '../css/etcCss.css'
import {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import { func } from "prop-types";
function Mypage(){

    let navigate = useNavigate();

    let state = useSelector((state) => {
        return state;
    });

    let [check, setCheck] = useState(0);

    return(

        <div className={"mypage"}>

            <div className={"mypage-header"}>
                {/* <p></p> */}
                <p>{state.userInfo.name} </p> <br/>
            </div>
            
            <div className={"mypagebody"}>

                <div className="card">
                    <div className="card-header"><p>Menu</p></div>
                    <ul className="list-group list-group-flush">
                        <li className="list-group-item" onClick={ ()=>{setCheck(0)} }> </li>
                        <li className="list-group-item" onClick={ ()=>{setCheck(1)} }> </li>
                        <li className="list-group-item" onClick={ ()=>{setCheck(2)} }>1:1 </li>
                    </ul>

                    <button type="button" className="btn btn-dark logout" onClick={ ()=>{
                        localStorage.setItem('userId', '');
                        navigate('/');
                    }}>LogOut</button>
                </div>



                <div className={"tabct"}>
                    <TabContext check={check}/>
                </div>
            </div>

        </div>

    );
}

function TabContext({check}) {
    return (
        <div className={"tabct"}>
            {[<Profile/>,<Management/>,<Inquiry/>][check]}
        </div>

    );
}

//  Tab
function Profile(){

    let state = useSelector((state) => {
        return state;
    });

    let tf = true;
    let u = '';
    function whoislogin(){

        let n = state.userInfo
        let i;
        for(i = 0; i < n.length && tf == true; i++){
            if(n[i].userid == localStorage.getItem('userId')){
                u = n[i];
                break;
            }
        }
    }

    return(
        <div>
            <img className={"profileImg"} src="https://paidion.org/img/base/icon_login.png"/>
            <h1 className={"profileTitle"}> </h1>
            <hr/>

            <div>
                {whoislogin()}
                <table className={"profileTable"}>
                    <tr>
                        <td></td>
                        <td>{u.userid}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>{u.name}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>{u.email}</td>
                    </tr>
                </table>

            </div>

        </div>
    );
}

//  Tab
function Management(){

    let state = useSelector((state) => {
        return state;
    });

    return(
        <div>
            <img className={"profileImg"} src="https://cdn.pixabay.com/photo/2017/03/29/04/09/shopping-icon-2184065_960_720.png"/>
            <h1 className={"profileTitle"}> </h1>
            <hr/><br/>
        </div>
    );
}

//1:1 Tab
